    clip-path: polygon(50% 50%, 100% 50%, 100% 50%, 100% 50%, 100% 50%, 100% 100%, 50% 100%, 0% 100%, 0% 100%);
  }

  70% {
    clip-path: polygon(50% 50%, 50% 100%, 50% 100%, 50% 100%, 50% 100%, 50% 100%, 50% 100%, 0% 100%, 0% 50%);
  }

  80% {
    clip-path: polygon(50% 50%, 0% 100%, 0% 100%, 0% 100%, 0% 100%, 0% 100%, 0% 100%, 0% 100%, 0% 50%);
  }

  90% {
    transform: rotate(360deg);
    clip-path: polygon(50% 50%, 0% 50%, 0% 50%, 0% 50%, 0% 50%, 0% 50%, 0% 50%, 0% 50%, 0% 50%);
  }

  100% {
    clip-path: polygon(50% 50%, 0% 50%, 0% 50%, 0% 50%, 0% 50%, 0% 50%, 0% 50%, 0% 50%, 0% 50%);
  }
}
/* checkbox, radio, switch */
.checkbox,
.radio,
.switch {
  direction: ltr;
  inline-size: auto;
  block-size: auto;
  line-height: normal;
  white-space: nowrap;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
}
:is(.checkbox, .radio) > input {
  inline-size: 1.5rem;
  block-size: 1.5rem;
  opacity: 0;
}
.switch > input {
  inline-size: 3.25rem;
  block-size: 2rem;
  opacity: 0;
}
:is(.checkbox, .radio, .switch) > span {
  display: inline-flex;
  align-items: center;
  color: var(--on-surface);
  font-size: 0.875rem;
}
:is(.checkbox, .radio) > span:not(:empty) {
  padding-inline-start: 0.25rem;
}
:is(.checkbox, .radio, .switch) > span::before,
.icon > span > i,
:is(.checkbox, .radio) > span::after {
  content: '';
  inline-size: 1.5rem;
  block-size: 1.5rem;
  box-sizing: border-box;
  margin: 0 auto;
  outline: none;
  color: var(--primary);
  position: absolute;
  inset: auto auto auto -1.5rem;
  border-radius: 50%;
  user-select: none;
  z-index: 1;
}
.switch > span::before,
.switch.icon > span > i {
  position: absolute;
  inset: 50% auto auto 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all var(--speed2);
  font-size: 1rem;
  user-select: none;
  min-inline-size: auto;
  content: "";
  color: var(--surface-variant);
  background-color: var(--outline);
}
.switch > span::before,
.switch.icon > span > i {
  transform: translate(-3rem, -50%) scale(0.6);
}
.switch.icon > span > i {
  transform: translate(-3rem, -50%) scale(1);
}
.checkbox > span::before {
  content: "check_box_outline_blank";
}
.checkbox > input:checked + span::before {
  content: "check_box";
  font-variation-settings: "FILL" 1;
}
.checkbox > input:indeterminate + span::before {
  content: "indeterminate_check_box";
}
.radio > span::before {
  content: "radio_button_unchecked";
}
.radio > input:checked + span::before {
  content: "radio_button_checked";
}
:is(.radio, .checkbox, .switch).icon > span::before {
  content: "" !important;
  font-variation-settings: unset !important;
}
:is(.checkbox, .radio) > span::after {
  transition: all var(--speed1);
  background-color: currentColor;
  box-shadow: 0 0 0 0 currentColor;
  opacity: 0;
}
:is(.checkbox, .radio):is(:hover) > input:not(:disabled) + span::after,
:is(.checkbox, .radio) > input:not(:disabled):is(:focus) + span::after {
  box-shadow: 0 0 0 0.5rem currentColor;
  opacity: 0.1;
}
.switch > input:not(:disabled):is(:focus, :hover) + span::before,
.switch.icon > input:not(:disabled):is(:focus, :hover) + span > i {
  box-shadow: 0 0 0 0.5rem var(--active);
}
:is(.checkbox, .radio) > input:checked + span::before,
:is(.checkbox, .radio).icon > input:checked + span > i {
  color: var(--primary);
}
.icon > input:checked + span > i:first-child,
.icon > span > i:last-child {
  opacity: 0;
}
.icon > input:checked + span > i:last-child,
.icon > span > i:first-child {
  opacity: 1;
}
.switch > input:checked + span::after {
  border: none;
  background-color: var(--primary);
}
.switch > input:checked + span::before,
.switch.icon > input:checked + span > i {
  content: "check";
  color: var(--primary);
  background-color: var(--on-primary);
}
.switch > input:checked + span::before,
.switch.icon > input:checked + span > i {
  transform: translate(-1.75rem, -50%) scale(1);
}
:is(.checkbox, .radio, .switch) > input:disabled + span {
  opacity: 0.5;
  cursor: not-allowed;
}
.switch > span::after {
  content: "";
  position: absolute;
  inset: 50% auto auto 0;
  background-color: var(--active);
  border: 0.125rem solid var(--outline);
  box-sizing: border-box;
  inline-size: 3.25rem;
  block-size: 2rem;
  border-radius: 2rem;
}
.switch > span::after {
  transform: translate(-3.25rem, -50%);
}
.field > :is(nav, .row) {
  flex-grow: 1;
  padding: 0 1rem;
}
.field.round > :is(nav, .row) {
  flex-grow: 1;
  padding: 0 1.5rem;
}
[dir=rtl] .switch {
  transform: scale(-1);
}
[dir=rtl] .switch > span::before,
[dir=rtl] .switch.icon > span > i {
  transform: translate(-3rem, -50%) scale(-0.6);
}
[dir=rtl] .switch.icon > span > i {
  transform: translate(-3rem, -50%) scale(-1);
}
[dir=rtl] .switch > input:checked + span::before,
[dir=rtl] .switch.icon > input:checked + span > i {
  transform: translate(-1.75rem, -50%) scale(-1);
}
.switch > :focus-visible + span::after {
  outline: 0.125rem solid var(--primary);
  outline-offset: 0.25rem;
}
:is(.checkbox, .radio) > :focus-visible + span::before {
  outline: 0.125rem solid var(--primary);
  outline-offset: 0.375rem;
}
.slider {
  ---start: 0%;
  ---end: 0%;
  ---value1: "";
  ---value2: "";

  display: flex;
  align-items: center !important;
  inline-size: auto;
  block-size: 1.25rem;
  margin: 1.125rem;
  flex: none;
  direction: ltr;
}
[dir=rtl] .slider {
  transform: scaleX(-1);
}
.slider.vertical {
  flex-direction: row !important;
  margin: 0.5rem auto !important;
  padding: 50% 0;
  transform: rotate(-90deg);
  inline-size: 100%;
}
.slider.small {
  inline-size: 4rem;
}
.slider.medium {
  inline-size: 8rem;
}
.slider.large {
  inline-size: 12rem;
}
.slider > input {
  appearance: none;
  box-shadow: none;
  border: none;
  outline: none;
  pointer-events: none;
  inline-size: 100%;
  block-size: 1rem;
  background: none;
  z-index: 1;
  padding: 0;
  margin: 0;
  transform: rotate(0deg);
}
.slider > input:only-of-type {
  pointer-events: all;
}
.slider > input + input {
  position: absolute;
}
.slider > input::-webkit-slider-thumb {
  appearance: none;
  box-shadow: none;
  border: none;
  outline: none;
  pointer-events: all;
  block-size: 2.75rem;
  inline-size: 0.25rem;
  border-radius: 0.25rem;
  background: var(--primary);
  cursor: grab;
  margin: 0;
}
.slider > input::-webkit-slider-thumb:active {
  cursor: grabbing;
}
.slider > input::-moz-range-thumb {
  appearance: none;
  box-shadow: none;
  border: none;
  outline: none;
  pointer-events: all;
  block-size: 2.75rem;
  inline-size: 0.25rem;
  border-radius: 0.25rem;
  background: var(--primary);
  cursor: grab;
  margin: 0;
}
.slider > input::-moz-range-thumb:active {
  cursor: grabbing;
}
.slider > input:not(:disabled):is(:focus)::-webkit-slider-thumb {
  transform: scaleX(0.6);
}
.slider > input:not(:disabled):is(:focus)::-moz-range-thumb {
  transform: scaleX(0.6);
}
.slider > input:disabled {
  cursor: not-allowed;
  opacity: 1;
}
.slider > input:disabled::-webkit-slider-thumb {
  background: #9E9E9E;
  cursor: not-allowed;
}
.slider > input:disabled::-moz-range-thumb {
  background: #9E9E9E;
  cursor: not-allowed;
}
.slider > input:disabled ~ span {
  background: #9E9E9E;
}
.slider > span {
  position: absolute;
  block-size: 1rem;
  border-radius: 1rem 0 0 1rem;
  background: var(--primary);
  z-index: 0;
  inset: calc(50% - 0.5rem) var(---end) auto var(---start);
  clip-path: polygon(0 0, calc(100% - 0.5rem) 0, calc(100% - 0.5rem) 100%, 0 100%);
}
.slider > input + input ~ span {
  border-radius: 0;
  clip-path: polygon(0.5rem 0, max(0.5rem, calc(100% - 0.5rem)) 0, max(0.5rem, calc(100% - 0.5rem)) 100%, 0.5rem 100%);
}
.field > .slider {
  inline-size: 100%;
}
.slider::before {
  content: "";
  position: absolute;
  inline-size: 100%;
  block-size: 1rem;
  border-radius: 1rem;
  background: var(--primary-container);
  clip-path: polygon(calc(var(---start) - 0.5rem) 0, 0 0, 0 100%, calc(var(---start) - 0.5rem) 100%, calc(var(---start) - 0.5rem) 0, calc(100% - var(---end) + 0.5rem) 0, 100% 0, 100% 100%, calc(100% - var(---end) + 0.5rem) 100%, calc(100% - var(---end) + 0.5rem) 0);
}
.slider:has(> [disabled])::before {
  background: var(--active);
}
.slider > .tooltip {
  visibility: hidden !important;
  opacity: 0 !important;
  inset: 0 auto auto calc(100% - var(---end));
  border-radius: 2rem;
  transition: top var(--speed2) ease, opacity var(--speed2) ease;
  transform: translate(-50%, -50%) !important;
  padding: 0.75rem 1rem;
}
[dir=rtl] .slider > .tooltip {
  transform: translate(-50%, -50%) scaleX(-1) !important;
}
.slider > .tooltip + .tooltip {
  inset: 0.25rem calc(100% - var(---start)) auto auto;
  transform: translate(50%, -50%) !important;
}
[dir=rtl] .slider > .tooltip + .tooltip {
  transform: translate(50%, -50%) scaleX(-1) !important;
}
.slider > .tooltip::before {
  content: var(---value1);
}
.slider > .tooltip + .tooltip::before {
  content: var(---value2);
}
.slider > :focus ~ .tooltip {
  inset-block-start: -1rem !important;
  opacity: 1 !important;
  visibility: visible !important;
}
.slider.vertical > .tooltip {
  display: none;
}
:is(nav, .row, .field) > .slider:not(.circle, .small, .medium, .large) {
  flex: auto;
}
.slider.max,
.slider.max.vertical,
.slider.max > input,
.slider.max.vertical > input {
  all: unset;
  margin: 0 !important;
  position: absolute;
  color: var(--primary);
  inset: 0;
  border-radius: inherit;
  overflow: hidden;
  z-index: 2;
  cursor: grab;
  inline-size: 100%;
  block-size: 100%;
}
.slider.max::before {
  display: none;
}
.slider.max.vertical > input {
  writing-mode: vertical-lr;
  transform: rotate(-180deg);
}
.slider.max > input::-webkit-slider-thumb {
  opacity: 0;
  inline-size: 1rem;
  block-size: 100vh;
  transform: none !important;
}
.slider.max > input::-moz-range-thumb {
  opacity: 0;
  inline-size: 1rem;
  block-size: 100vh;
  transform: none !important;
}
.slider.max > span {
  block-size: auto !important;
  inset: 0 var(---end) 0 var(---start);
  clip-path: none;
  background: currentcolor;
  border-radius: 0;
}
.slider.max.vertical > span {
  inset: var(---end) 0 var(---start) 0;
}
.slider > input:focus-visible::-webkit-slider-thumb {
  outline: 0.1875rem solid var(--primary);
  outline-offset: 0.25rem;
}
.slider > input:focus-visible::-moz-range-thumb {
  outline: 0.1875rem solid var(--primary);
  outline-offset: 0.25rem;
}
.slider.max > input:focus-visible {
  outline: 0.1875rem solid var(--primary);
  outline-offset: -0.125rem;
}
@media (pointer: coarse) {
  .slider > :hover ~ .tooltip {
    inset-block-start: -1rem !important;
    opacity: 1 !important;
    visibility: visible !important;
  }
}
.snackbar {
  position: fixed;
  inset: auto auto 6rem 50%;
  inline-size: 80%;
  block-size: auto;
  z-index: 200;
  visibility: hidden;
  display: flex;
  box-shadow: var(--elevate2);
  color: var(--inverse-on-surface);
  background-color: var(--inverse-surface);
  padding: 1rem;
  opacity: 1;
  cursor: pointer;
  text-align: start;
  align-items: center;
  border-radius: 0.25rem;
  gap: 0.5rem;
  transition: all var(--speed2);
  transform: translate(-50%, 1rem);
  opacity: 0;
}
.snackbar.top {
  inset: 6rem auto auto 50%;
}
.snackbar.active {
  visibility: visible;
  transform: translate(-50%, 0);
  opacity: 1;
}
.snackbar > .max {
  flex: auto;
}
@media only screen and (min-width: 993px) {
  .snackbar {
    inline-size: 40%;
  }
}
table {
  inline-size: 100%;
  border-spacing: 0;
  font-size: 0.875rem;
  text-align: start;
}
.scroll > table,
table :is(thead, tbody, tfoot, tr, th, td) {
  background-color: inherit;
  color: inherit;
}
:is(th, td) {
  inline-size: auto;
  text-align: inherit;
  padding: 0.5rem;
}
:is(th, td) > * {
  vertical-align: middle;
}
table.border > tbody > tr:not(:last-child) > td,
thead > tr > th {
  border-block-end: 0.0625rem solid var(--outline);
}
tfoot > tr > th {
  border-block-start: 0.0625rem solid var(--outline);
}
table.stripes > tbody > tr:nth-child(odd) {
  background-color: var(--active);
}
table.no-space :is(th, td) {
  padding: 0;
}
table.medium-space :is(th, td) {
  padding: 0.75rem;
}
table.large-space :is(th, td) {
  padding: 1rem;
}
table > .fixed,
th.fixed {
  position: sticky;
  z-index: 1;
  inset-block-start: 0;
}
tfoot.fixed,
tfoot th.fixed {
  inset-block-end: 0;
}
:is(td, th).min {
  inline-size: 0.1%;
  white-space: nowrap;
}
.tabs {
  display: flex;
  white-space: nowrap;
  border-block-end: 0.0625rem solid var(--surface-variant);
}
.tabs:not(.left-align, .right-align, .center-align) {
  justify-content: space-around;
}
.tabs > a {
  display: flex;
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--on-surface-variant);
  padding: 0.5rem 1rem;
  text-align: center;
  min-block-size: 3rem;
  inline-size: 100%;
  gap: 0.25rem;
}
.tabs.small > a {
  min-block-size: 2rem;
}
.tabs.large > a {
  min-block-size: 4rem;
}
.tabs > a.active,
.tabs > a.active > i {
  color: var(--primary);
}
.tabs > a.active::before {
  content: '';
  position: absolute;
  inset: auto 0 0 0;
  block-size: 0.125rem;
  background-color: var(--primary);
}
.tabs.min > a.active::before {
  margin: 0 auto;
  max-inline-size: min(100%, 4rem);
}
.tabs:is(.left-align, .center-align, .right-align) > a {
  inline-size: auto;
}
.tooltip {
  ---space: -0.5rem;

  visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  background-color: var(--inverse-surface);
  color: var(--inverse-on-surface);
  font-size: 0.75rem;
  text-align: center;
  border-radius: 0.25rem;
  padding: 0.5rem;
  position: absolute;
  z-index: 3;
  inset: 0 auto auto 50%;
  inline-size: auto;
  white-space: nowrap;
  font-weight: 500;
  opacity: 0;
  transition: all var(--speed2);
  line-height: normal;
  transform: translate(-50%, -100%) scale(0.9);
}
.tooltip.left {
  inset: 50% auto auto 0;
  transform: translate(-100%, -50%) scale(0.9);
}
.tooltip.right {
  inset: 50% 0 auto auto;
  transform: translate(100%, -50%) scale(0.9);
}
.tooltip.bottom {
  inset: auto auto 0 50%;
  transform: translate(-50%, 100%) scale(0.9);
}
.tooltip.small {
  inline-size: 8rem;
  white-space: normal;
}
.tooltip.medium {
  inline-size: 12rem;
  white-space: normal;
}
.tooltip.large {
  inline-size: 16rem;
  white-space: normal;
}
:hover > .tooltip {
  visibility: visible;
  opacity: 1;
  transform: translate(-50%, -100%) scale(1);
}
:hover > .tooltip.left {
  transform: translate(-100%, -50%) scale(1);
}
:hover > .tooltip.right {
  transform: translate(100%, -50%) scale(1);
}
:hover > .tooltip.bottom {
  transform: translate(-50%, 100%) scale(1);
}
.tooltip.no-space {
  ---space: 0;
}
.tooltip.medium-space {
  ---space: -1rem;
}
.tooltip.large-space {
  ---space: -1.5rem;
}
.tooltip:not(.left, .right, .bottom) {
  margin-block-start: var(---space) !important;
}
.tooltip.left,
.tooltip.right {
  margin-inline: var(---space) !important;
}
.tooltip.bottom {
  margin-block-end: var(---space) !important;
}
menu:active ~ .tooltip,
:is(button, .button):focus > menu ~ .tooltip,
.field > :focus ~ menu ~ .tooltip {
  visibility: hidden;
}
.slider > .tooltip {
  ---space: -1.25rem;
}
.slider.vertical > .tooltip {
  ---space: -0.75rem;
}
.slider.vertical > .tooltip:is(.left, .right) {
  ---space: -0.5rem;
}
.tooltip.max {
  display: block;
  font-size: inherit;
  white-space: normal;
  text-align: start;
  inline-size: 20rem;
  border-radius: 0.5rem;
  padding: 1rem;
  box-shadow: var(--elevate2);
}
[class*=blur],
[class*=blur].light {
  ---blur: 1rem;
  -webkit-backdrop-filter: blur(var(---blur));
  backdrop-filter: blur(var(---blur));
  color: var(--on-surface);
